import listCell from '@/components/mix-list-cell';
import uniTag from "@/components/uni-ui/uni-tag/uni-tag.vue"
import {
	mapState
} from 'vuex';
export default {
	components: {
		listCell,
		uniTag
	},
	data() {
		return {
			datas: [],
			fans: 0,
			attention:0,
			id: ''
		}
	},
	onLoad(options) {
		uni.showLoading({
			title: '正在加载'
		})
		console.log(options)
		this.id = options.id
		this.loadData()
	},
	onShow() {
		// this.getAttentionNum(this.datas.id)
		// this.getFanNum(this.datas.id)
	},
	computed: {
		...mapState(['hasLogin', 'userInfo']),
	},
	methods:{
		async loadData() {
			console.log(this.id)
			const res = await this.$api.request({
				url: '/user/' + this.id,
				method: 'GET'
			})
			console.log(res)
			this.datas = res.data
			uni.hideLoading()
		},
		async getFanNum() {
			const res = await this.$api.request({
				url: '/user/countFans',
				data: {
					id: this.id
				}
			})
			this.fans = res.data
		},
		async getAttentionNum() {
			const res = await this.$api.request({
				url: '/user/countAttentions',
				data: {
					id: this.id
				}
			})
			this.attention = res.data
			
		},
		navTo(url) {
			if (!this.hasLogin) {
				url = '/pages/public/login';
			}
			uni.navigateTo({
				url
			})
		},
	}
}
